
































































































































































































































































































    export default {
        data() {
            return {
                uid: 0,
                list: [
					{
						goods_id: 0,
						goods_name: "",
						cate_id:"",
						wIndex: -1,
						goods_quantity:0,
						total_price:0,
						price:0,
						unit_name:''
					}
				],
				workmanship_data: [
					{
						workmanship_id: 0,
						project_name: "",
						cate_id:"",
						gIndex: -1,
						quantity:0,
						workmanship_price:0,
						workmanship_sub_total:''
					}
				],
                date: "",
                tel: "",
                clientId: 0,
                clientName: "",
                showClient: false,
                clientList: [],
                textClient: "",
                showGood: false,
				gongyiGood:false,
                goodList: [],
                textGood: "",
                itemIndex: 0,
				itemgIndex:0,
                preventEvent: false,
				kehu_num : 1,
				remark:'',
				time:'',
				term_date:'',
				warehouse: [],
				warehouse_gongyi:[],
				gongyiList:[],
				
				name:'',//客户姓名
				address:'',//客户地址
				mobile_one:'',//联系方式1
				mobile_two:'',//联系方式2
				
				discount:'',//折扣
				remarks:'',//备注
				
				total_money:'',//商品总价
				total_count:'',//商品总数
				workmanship_total_money:'',//工艺总价
				workmanship_total_count:'',//工艺总数
				order_total_money:'',//总价
				
            }
        },
        mounted() {
        },
        onLoad(option) {
			this.uid = this.common.get("uid")
			// 销售
			this.http({
			    url: this.api.get_user_list,
			    method: "POST",
			    data: {
			        uid: this.uid,
			    },
			    success: res => {
			        this.clientList = res
			    }
			})
			// 商品分类接口
			this.http({
			    url: this.api.get_goods_cate,
			    method: "POST",
			    data: {
			        uid: this.uid
			    },
			    success: res => {
			        this.warehouse = res
			    }
			})
			// 工艺分类接口
			this.http({
			    url: this.api.get_workmanship_cate,
			    method: "POST",
			    data: {
			        uid: this.uid
			    },
			    success: res => {
			        this.warehouse_gongyi = res
			    }
			})
            
			if(option.data) {
				let data = JSON.parse(decodeURIComponent(option.data))
				this.edit_id = data
				setTimeout(() =>{
					// 进入页面
					this.http({
					    url: this.api.budget_info,
					    method: "POST",
					    data: {
					        uid: this.uid,
							id:this.edit_id
					    },
					    success: res => {
							this.name = res.name
							this.address = res.address
							this.mobile_one = res.mobile_one
							this.mobile_two = res.mobile_two
							this.discount = Number(res.discount)*100
							this.remarks = res.remarks
							this.total_money = res.total_money//商品总价
							this.total_count = res.total_count//商品总数
							this.workmanship_total_money = res.workmanship_total_money//工艺总价
							this.workmanship_total_count = res.workmanship_total_count//工艺总数
							this.order_total_money = res.order_total_money//总价
							this.list = res.goods_data
							this.remarkImage = res.amount_image
							this.list.map((item) => {
								item.goods_name = item.name
							})
							if(this.list.length == 0) {
								let item = {
								    goods_id: 0,
								    goods_name: "",
								    cate_id:"",
								    wIndex: -1,
								    goods_quantity:0,
								    total_price:0,
								    price:0,
								    unit_name:''
								}
								this.list.push(item)
							}
							for(let i = 0; i<this.warehouse.length; i++) {
								for(let j = 0; j<this.list.length; j++) {
									if (this.warehouse[i].id == this.list[j].cate_id) {
										this.list[j].wIndex = i
									}
								}
							}
							this.workmanship_data = res.workmanship_data
							if(this.workmanship_data.length == 0) {
								let item = {
								    workmanship_id: 0,
								    project_name: "",
								    cate_id:"",
								    gIndex: -1,
								    quantity:0,
								    workmanship_price:0,
								    workmanship_sub_total:''
								}
								this.workmanship_data.push(item)
							}
							for(let i = 0; i<this.warehouse_gongyi.length; i++) {
								for(let j = 0; j<this.workmanship_data.length; j++) {
									if (this.warehouse_gongyi[i].id == this.workmanship_data[j].cate_id) {
										this.workmanship_data[j].gIndex = i
									}
								}
							}
							
							this.clientId = res.create_user_id
							this.clientList.map((item,index) =>{
								if(item.id == res.create_user_id) {
									this.clientName = item.name
								}
							})
							this.is_rowruler = res.is_rowruler
							this.rowruler.map((item,index)=>{
								if(item.id == res.is_rowruler) {
									item.checked = true
								}
							})
							this.is_accessories = res.is_accessories
							this.accessories.map((item,index)=>{
								if(item.id == res.is_accessories) {
									item.checked = true
								}
							})
					    }
					})
				},500)
			}
			
        },
        methods: {
			
			// 客户名称搜索
			searchClient() {
			    this.http({
			        url: this.api.get_user_list,
			        method: "POST",
			        data: {
			            uid: this.uid,
			            search: this.textClient
			        },
			        success: res => {
			            this.clientList = res
			        }
			    })
			},
			
			// 选择商品分类
			changeWarehouse(e,index) {
				this.list[index].wIndex = e.target.value
				this.list[index].cate_id = this.warehouse[e.target.value].id
				// this.list[index].cangku = this.warehouse[e.target.value].warehouse_name
			},
			// 选择工艺
			changeWarehouse_gongyi(e,index) {
				this.workmanship_data[index].gIndex = e.target.value
				this.workmanship_data[index].cate_id = this.warehouse_gongyi[e.target.value].id
				// this.list[index].cangku = this.warehouse[e.target.value].warehouse_name
			},
			// 名称选择
            openModal(name, index) {
                this.preventEvent = true
                if (name == "client") {
                    this.showClient = true
					this.kehu_num = 1
                } else if (name == "good") {
					this.itemIndex = index
					if(this.list[index].cate_id !=='') {
						this.showGood = true
						this.http({
						    url: this.api.get_goods_data,
						    method: "POST",
						    data: {
						        uid: this.uid,
								cate_id:this.list[index].cate_id
						    },
						    success: res => {
						        this.goodList = res
						    }
						})
					} else {
						uni.showToast({
							title:'请先选择商品分类',
							icon:'none',
							duration: 2000
						})
					}
					
                } else if(name =="clientlist") {
					this.itemIndex = index
					this.showClient = true
					this.kehu_num = 2
				}else if (name == "gongyi") {
					this.itemgIndex = index
					if(this.workmanship_data[index].cate_id !=='') {
						this.gongyiGood = true
						this.http({
						    url: this.api.get_workmanship_data,
						    method: "POST",
						    data: {
						        uid: this.uid,
								cate_id:this.workmanship_data[index].cate_id
						    },
						    success: res => {
						        this.gongyiList = res
						    }
						})
					} else {
						uni.showToast({
							title:'请先选择工艺分类',
							icon:'none',
							duration: 2000
						})
					}
					
                }
            },
			// 弹窗关闭按钮
            closeModal(name) {
                this.preventEvent = false
                if (name == "client") {
                    this.showClient = false
                } else if (name == "good") {
                    this.showGood = false
                } else if (name == 'clientlist') {
					this.showClient = false
				}else if (name == 'gongyi') {
					this.gongyiGood = false
				}
            },
			// 客户名称搜索
            searchClient() {
                this.http({
                    url: this.api.goodsGet_apply,
                    method: "POST",
                    data: {
                        uid: this.uid,
                        name: this.textClient
                    },
                    success: res => {
                        this.clientList = res
                    }
                })
            },
			// // 商品搜索
   //          searchGood(index) {
   //              this.http({
   //                  url: this.api.goodsList,
   //                  method: "POST",
   //                  data: {
   //                      uid: this.uid,
			// 			name: this.textGood
   //                  },
   //                  success: res => {
   //                      this.goodList = res
   //                  }
   //              })
   //          },
			// 客户选择点击
            selectClient(i) {
				this.clientId = this.clientList[i].id
				this.clientName = this.clientList[i].name
				this.showClient = false
                this.preventEvent = false
            },
			// 商品选择点击
            selectGood(i,e) {
                this.prevent = false
                this.showGood = false
                let gid = this.goodList[i].id
                let exist = this.isExist(gid)
                if (exist) {
                    uni.showToast({
                        title: "请勿重复添加商品",
                        icon: "none"
                    })
                    return
                }
				this.list[this.itemIndex].goods_id = this.goodList[i].id
				this.list[this.itemIndex].goods_name = this.goodList[i].name
				this.list[this.itemIndex].unit_name = this.goodList[i].unit_name
            },
			// 工艺选择点击
			selectGongyi(i,e) {
			    this.prevent = false
			    this.gongyiGood = false
			    let gid = this.gongyiList[i].id
			    let exist = this.isExistGongyi(gid)
			    if (exist) {
			        uni.showToast({
			            title: "请勿重复添加工艺",
			            icon: "none"
			        })
			        return
			    }
				this.workmanship_data[this.itemgIndex].workmanship_id = this.gongyiList[i].id
				this.workmanship_data[this.itemgIndex].project_name = this.gongyiList[i].project_name
				this.workmanship_data[this.itemgIndex].unit_name = this.gongyiList[i].unit_name
			},
            close() {
                this.preventEvent = false
            },
			// 添加商品按钮
            add(eve) {
				console.log('eve',eve)
				if(eve == 'shop') {
					let item = {
					    goods_id: 0,
					    goods_name: "",
					    cate_id:"",
					    wIndex: -1,
					    goods_quantity:0,
					    total_price:0,
					    price:0,
					    unit_name:''
					}
					this.list.push(item)
				} else if(eve == 'gongyi') {
					let item = {
					    workmanship_id: 0,
					    project_name: "",
					    cate_id:"",
					    gIndex: -1,
					    quantity:0,
					    workmanship_price:0,
					    workmanship_sub_total:''
					}
					this.workmanship_data.push(item)
				}
                
            },
			// 商品删除按钮
            del(index,eve) {
				if (eve == 'shop') {
					if (this.list.length == 1) {
						uni.showToast({
							title:'最少添加一个商品',
							icon:'none',
							duration: 2000
						})
					} else{
						this.list.splice(index, 1)
						this.total_money = 0
						this.total_count = 0
						this.order_total_money = 0
						this.list.map((item,index) =>{
							this.total_money = Number(this.total_money)+Number(item.total_price)
							this.total_count = Number(this.total_count)+Number(item.goods_quantity)
						})
						if(this.discount == '') {
							this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
						} else {
							this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
						}
					}
				} else if(eve == 'gongyi') {
					if (this.workmanship_data.length == 1) {
						uni.showToast({
							title:'最少添加一个工艺',
							icon:'none',
							duration: 2000
						})
					} else{
						this.workmanship_data.splice(index, 1)
						this.workmanship_total_count = 0
						this.workmanship_total_money = 0
						this.order_total_money = 0
						this.workmanship_data.map((item,index) =>{
							this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
							this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
						})
						if(this.discount == '') {
							this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
						} else {
							this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
						}
					}
				}
				
            },
			// 商品数量  总价
			count(e, i ,j) {
			    if (e == "") {
			        e = 0
			    }
				if(j == 'shop') {
					this.list[i].goods_quantity = e
					this.list[i].total_price = (this.list[i].price * this.list[i].goods_quantity).toFixed(2)
					this.total_money = 0
					this.total_count = 0
					this.order_total_money = 0
					this.list.map((item,index) =>{
						this.total_money = Number(this.total_money)+Number(item.total_price)
						this.total_count = Number(this.total_count)+Number(item.goods_quantity)
					})
					if(this.discount == '') {
						this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
					} else {
						this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
					}
				} else {
					this.workmanship_total_count = 0
					this.workmanship_total_money = 0
					this.order_total_money = 
					this.workmanship_data[i].quantity = e
					this.workmanship_data[i].workmanship_sub_total = (this.workmanship_data[i].workmanship_price * this.workmanship_data[i].quantity).toFixed(2)
					this.workmanship_data.map((item,index) =>{
						this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
						this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
					})
					if(this.discount == '') {
						this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
					} else {
						this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
					}
				}
			    
			},
			// 商品单价
			price(e, i ,j) {
			    if (e == "") {
			        e = 0
			    }
				if(j == 'shop') {
					this.list[i].price = e
					this.list[i].total_price = (this.list[i].price * this.list[i].goods_quantity).toFixed(2)
					this.total_money = 0
					this.total_count = 0
					this.order_total_money = 0
					this.list.map((item,index) =>{
						this.total_money = Number(this.total_money)+Number(item.total_price)
						this.total_count = Number(this.total_count)+Number(item.goods_quantity)
					})
					if(this.discount == '') {
						this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
					} else {
						this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
					}
				} else {
					this.workmanship_data[i].workmanship_price = e
					this.workmanship_data[i].workmanship_sub_total = (this.workmanship_data[i].workmanship_price * this.workmanship_data[i].quantity).toFixed(2)
					this.workmanship_total_count = 0
					this.workmanship_total_money = 0
					this.order_total_money = 0
					this.workmanship_data.map((item,index) =>{
						this.workmanship_total_count = Number(this.workmanship_total_count)+Number(item.quantity)
						this.workmanship_total_money = Number(this.workmanship_total_money)+Number(item.workmanship_sub_total)
					})
					if(this.discount == '') {
						this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
					} else {
						this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
					}
				}
			    
			},
			// 折扣
			discount_text(e) {
				let a = Number(e)*1
				console.log(a)
				if(a>100) {
					this.discount = 100
				} else if(a<0) {
					this.discount = 1
				}
				if(this.discount == 0) {
					this.order_total_money = (Number(this.total_money) + Number(this.workmanship_total_money))*1
				} else {
					this.order_total_money = (Number(this.total_money) + (Number(this.workmanship_total_money))*(this.discount/100)).toFixed(2)
				}
			},
            isExist(goodId) {
                let bool = false
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].goods_id == goodId) {
                        bool = true
                    }
                }
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id == goodId) {
				        bool = true
				    }
				}
                return bool
            },
			isExistGongyi(goodId) {
			    let bool = false
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id == goodId) {
				        bool = true
				    }
				}
			    return bool
			},
            updatePriceArr() {
                let arr = []
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].goods_id != 0) {
                        arr.push(this.list[i])
                    }
                }
                return arr
            },
			updatePriceArrGongyi() {
			    let arr = []
				for (let i = 0; i < this.workmanship_data.length; i++) {
				    if (this.workmanship_data[i].workmanship_id != 0) {
				        arr.push(this.workmanship_data[i])
				    }
				}
			    return arr
			},
			
			// 返回上一页
			previous() {
				let pages = getCurrentPages(); // 当前页面
				let beforePage = pages[pages.length - 2]; // 上一页
				uni.navigateBack({
				    success: function() {
				        beforePage.onLoad(); // 执行上一页的onLoad方法
				    }
				});
			},
			// 确定添加按钮点击
            submit() {
				uni.showModal({
					title: '提示',
					content: '确定提交吗？',
					success: res => {
						if(res.confirm) {
							if(this.discount == '') {
								this.discount = ''
							} else {
								this.discount = this.discount/100
							}
							this.http({
							    url: this.api.budget_submit,
							    method: "POST",
							    data: {
									uid: this.uid,
									id:this.edit_id,
									name:this.name,//客户姓名
									address:this.address,//客户地址
									mobile_one:this.mobile_one,//联系方式1
									mobile_two:this.mobile_two,//联系方式2
									discount:this.discount,//折扣
									remarks:this.remarks,//备注
									create_user_id:this.clientId,
									total_money:this.total_money,
									total_count:this.total_count,
									workmanship_total_count:this.workmanship_total_count,
									workmanship_total_money:this.workmanship_total_money,
									order_total_money:this.order_total_money,
									goods_data:JSON.stringify(this.updatePriceArr(this.list)),
									workmanship_data:JSON.stringify(this.updatePriceArrGongyi(this.workmanship_data))
							    },
							    success: res => {
									uni.showToast({
										title:'编辑成功',
										icon:'none',
										duration: 2000
									})
									setTimeout(() =>{
										this.previous()
									},500) 
							    }
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
            }
        }
    }
